// Reviews mutations
export const useCreateReview = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (reviewData: CreateReviewDto) => reviewsApi.createReview(reviewData),
    onSuccess: (data) => {
      // –û–±–Ω–æ–≤–ª—è–µ–º –∫—ç—à –æ—Ç–∑—ã–≤–æ–≤ —Ä–µ—Å—Ç–æ—Ä–∞–Ω–∞
      queryClient.invalidateQueries({ 
        queryKey: ['reviews', 'restaurant', data.restaurant_id] 
      });
      // –û–±–Ω–æ–≤–ª—è–µ–º –º–æ–∏ –æ—Ç–∑—ã–≤—ã
      queryClient.invalidateQueries({ 
        queryKey: ['reviews', 'my'] 
      });
      // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ —Ä–µ—Å—Ç–æ—Ä–∞–Ω–µ (—Ä–µ–π—Ç–∏–Ω–≥ –º–æ–≥ –∏–∑–º–µ–Ω–∏—Ç—å—Å—è)
      queryClient.invalidateQueries({ 
        queryKey: ['restaurants', data.restaurant_id] 
      });
    },
  });
};

export const useUpdateReview = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({ reviewId, reviewData }: { reviewId: string; reviewData: UpdateReviewDto }) =>
      reviewsApi.updateReview(reviewId, reviewData),
    onSuccess: (data) => {
      // –û–±–Ω–æ–≤–ª—è–µ–º –∫—ç—à –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–≥–æ –æ—Ç–∑—ã–≤–∞
      queryClient.invalidateQueries({ 
        queryKey: ['reviews', data.review_id] 
      });
      // –û–±–Ω–æ–≤–ª—è–µ–º –æ—Ç–∑—ã–≤—ã —Ä–µ—Å—Ç–æ—Ä–∞–Ω–∞
      queryClient.invalidateQueries({ 
        queryKey: ['reviews', 'restaurant', data.restaurant_id] 
      });
      // –û–±–Ω–æ–≤–ª—è–µ–º –º–æ–∏ –æ—Ç–∑—ã–≤—ã
      queryClient.invalidateQueries({ 
        queryKey: ['reviews', 'my'] 
      });
      // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ —Ä–µ—Å—Ç–æ—Ä–∞–Ω–µ
      queryClient.invalidateQueries({ 
        queryKey: ['restaurants', data.restaurant_id] 
      });
    },
  });
};

export const useDeleteReview = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (reviewId: string) => reviewsApi.deleteReview(reviewId),
    onSuccess: (_, reviewId) => {
      // –£–¥–∞–ª—è–µ–º –∫–æ–Ω–∫—Ä–µ—Ç–Ω—ã–π –æ—Ç–∑—ã–≤ –∏–∑ –∫—ç—à–∞
      queryClient.removeQueries({ 
        queryKey: ['reviews', reviewId] 
      });
      // –û–±–Ω–æ–≤–ª—è–µ–º –≤—Å–µ —Å–≤—è–∑–∞–Ω–Ω—ã–µ –∑–∞–ø—Ä–æ—Å—ã
      queryClient.invalidateQueries({ 
        queryKey: ['reviews'] 
      });
      queryClient.invalidateQueries({ 
        queryKey: ['restaurants'] 
      });
    },
  });
};

